// Coupons [id] API v1
// GET /api/v1/revenue-engine/coupons/[id] - Get coupon by ID
// PUT /api/v1/revenue-engine/coupons/[id] - Update coupon
// DELETE /api/v1/revenue-engine/coupons/[id] - Delete coupon

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'

type Props = {
  params: Promise<{ id: string }>
}

// GET - Get coupon by ID
export async function GET(request: NextRequest, props: Props) {
  try {
    const { id } = await props.params
    
    const { data: coupon, error } = await supabaseAdmin
      .from('Coupon')
      .select('*')
      .eq('id', id)
      .single()
    
    if (error || !coupon) {
      return NextResponse.json(
        { success: false, error: 'Coupon not found' },
        { status: 404 }
      )
    }
    
    return NextResponse.json({
      success: true,
      data: { coupon },
    })
  } catch (error: any) {
    console.error(`GET /api/v1/revenue-engine/coupons/${(await props.params).id} error:`, error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to fetch coupon' },
      { status: 500 }
    )
  }
}

// PUT - Update coupon
export async function PUT(request: NextRequest, props: Props) {
  try {
    const { id } = await props.params
    const body = await request.json()
    
    const {
      code,
      name,
      description,
      discountType,
      discountValue,
      currency,
      appliesTo,
      productIds,
      collectionIds,
      minPurchaseAmount,
      maxDiscountAmount,
      usageLimit,
      usageLimitPerCustomer,
      validFrom,
      validUntil,
      status,
      firstTimeOnly,
    } = body
    
    // Check if coupon exists
    const { data: existingCoupon, error: fetchError } = await supabaseAdmin
      .from('Coupon')
      .select('id, subAccountId, code')
      .eq('id', id)
      .single()
    
    if (fetchError || !existingCoupon) {
      return NextResponse.json(
        { success: false, error: 'Coupon not found' },
        { status: 404 }
      )
    }
    
    // If code is being changed, check for duplicates
    if (code && code.toUpperCase() !== existingCoupon.code) {
      const { data: duplicateCoupon } = await supabaseAdmin
        .from('Coupon')
        .select('id')
        .eq('subAccountId', existingCoupon.subAccountId)
        .eq('code', code.toUpperCase())
        .neq('id', id)
        .single()
      
      if (duplicateCoupon) {
        return NextResponse.json(
          { success: false, error: 'A coupon with this code already exists' },
          { status: 409 }
        )
      }
    }
    
    // Validate discount type if provided
    if (discountType && !['percentage', 'fixed_amount'].includes(discountType)) {
      return NextResponse.json(
        { success: false, error: 'discountType must be "percentage" or "fixed_amount"' },
        { status: 400 }
      )
    }
    
    // Validate percentage discount if provided
    if (discountType === 'percentage' && discountValue !== undefined && (discountValue < 0 || discountValue > 100)) {
      return NextResponse.json(
        { success: false, error: 'Percentage discount must be between 0 and 100' },
        { status: 400 }
      )
    }
    
    // Build update object
    const updateData: any = { updatedAt: new Date().toISOString() }
    if (code !== undefined) updateData.code = code.toUpperCase()
    if (name !== undefined) updateData.name = name
    if (description !== undefined) updateData.description = description
    if (discountType !== undefined) updateData.discountType = discountType
    if (discountValue !== undefined) updateData.discountValue = parseFloat(discountValue)
    if (currency !== undefined) updateData.currency = currency
    if (appliesTo !== undefined) updateData.appliesTo = appliesTo
    if (productIds !== undefined) updateData.productIds = productIds
    if (collectionIds !== undefined) updateData.collectionIds = collectionIds
    if (minPurchaseAmount !== undefined) updateData.minPurchaseAmount = minPurchaseAmount
    if (maxDiscountAmount !== undefined) updateData.maxDiscountAmount = maxDiscountAmount
    if (usageLimit !== undefined) updateData.usageLimit = usageLimit
    if (usageLimitPerCustomer !== undefined) updateData.usageLimitPerCustomer = usageLimitPerCustomer
    if (validFrom !== undefined) updateData.validFrom = validFrom
    if (validUntil !== undefined) updateData.validUntil = validUntil
    if (status !== undefined) updateData.status = status
    if (firstTimeOnly !== undefined) updateData.firstTimeOnly = firstTimeOnly
    
    const { data: coupon, error } = await supabaseAdmin
      .from('Coupon')
      .update(updateData)
      .eq('id', id)
      .select()
      .single()
    
    if (error) throw error
    
    return NextResponse.json({
      success: true,
      data: { coupon },
    })
  } catch (error: any) {
    console.error(`PUT /api/v1/revenue-engine/coupons/${(await props.params).id} error:`, error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to update coupon' },
      { status: 500 }
    )
  }
}

// DELETE - Delete coupon
export async function DELETE(request: NextRequest, props: Props) {
  try {
    const { id } = await props.params
    
    const { data: coupon, error: fetchError } = await supabaseAdmin
      .from('Coupon')
      .select('id, usageCount')
      .eq('id', id)
      .single()
    
    if (fetchError || !coupon) {
      return NextResponse.json(
        { success: false, error: 'Coupon not found' },
        { status: 404 }
      )
    }
    
    // Soft delete by setting status to 'archived' if coupon has been used
    if (coupon.usageCount > 0) {
      const { error: updateError } = await supabaseAdmin
        .from('Coupon')
        .update({ status: 'archived', updatedAt: new Date().toISOString() })
        .eq('id', id)
      
      if (updateError) throw updateError
      
      return NextResponse.json({
        success: true,
        message: 'Coupon archived (has usage history)',
      })
    }
    
    // Hard delete if coupon has never been used
    const { error: deleteError } = await supabaseAdmin
      .from('Coupon')
      .delete()
      .eq('id', id)
    
    if (deleteError) throw deleteError
    
    return NextResponse.json({
      success: true,
      message: 'Coupon deleted successfully',
    })
  } catch (error: any) {
    console.error(`DELETE /api/v1/revenue-engine/coupons/${(await props.params).id} error:`, error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to delete coupon' },
      { status: 500 }
    )
  }
}
